@media only screen and (max-width: 500px) {
  .imgDiv {
    width: 100vw;
    height: 40vh;
    position: relative;
    text-align: center;
    color: white;
    background: rgb(98, 144, 27);  }
  .image1 {
    width: 100vw;
    height: 50vh;
  }
  .text {
    font-size: 25px;
    width: 90vw;
    height: 50vh;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: white;
    font-family: 'Crimson Text', serif;
    margin-top: 20%;
  }
  .mAndV {
    width: 100%;
    display: block;
  }
  .mission {
    width: 100%;
    height: auto;
    text-align: left;
    color: black;
    font-family: 'Playfair Display', serif;
    padding: 5%;
  }
  .vission{
    width: 100%;
    height: auto;
    text-align: left;
    color: black;
    border-radius: 1%;
    font-family: 'Playfair Display', serif;
    padding: 5%;
  }
  .texts {
    font-family: 'Crimson Pro', serif;
  }
  div .mission > h2 {
    font-size: 50px;
  }
  div .vission > h2 {
    font-size: 50px;
  }
}

